@font-face {
  font-family: "Kalameh-Regular";
  src: url("./assets/fonts/KalamehFaNum-Regular.ttf");
}
@font-face {
  font-family: "Kalameh-Bold";
  src: url("./assets/fonts/KalamehFaNum-Bold.ttf");
}
@font-face {
  font-family: "Kalameh-ExtraBold";
  src: url("./assets/fonts/KalamehFaNum-ExtraBold.ttf");
}
* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing:border-box;
  border: none;
  font-family: "Kalameh-Bold";
  direction: rtl;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}
body {
  padding: 0 1rem;
  overflow: hidden;
  user-select: none;
}
